<template>
  <div>
    <div class="title">
      更新知识库
    </div>
    <div class="WOBox">
        <div class="operation">
            <el-button type="warning" @click="FnAdd()">新增</el-button>
        </div>
        <!-- 表格 -->
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
            prop="date"
            label="历史发布">
                <template slot-scope="scope">
                    <div>{{scope.row.k_title}}</div>
                    <div>{{scope.row.k_date}}</div>
                </template>
            </el-table-column>
            <el-table-column
            label="操作">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row.k_id,scope.row.k_status)" type="text" size="small">{{scope.row.k_status ? '修改解决方案' : "上传解决方案"}}</el-button>
                    <el-button @click="Fngitodetails(scope.row.k_id)" type="text" size="small">查看</el-button>
                    <el-button type="text"  @click="gotoeditknowledge(scope.row.k_id)"  size="small">修改问题描述</el-button>
                    <el-button type="text" @click="Fndeleteknowledge(scope.row.k_id)" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="PageSize"
            :total="total">
        </el-pagination>
    </div>    
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
    data(){
        return{
            tableData: [],//这是列表编辑
            total:0,//分页-总条目
            currentPage:1,//分页-当前页数
            PageSize:7,//每页有多少条数
        };
    },
    methods:{
        handleClick(k_id,k_status){//点击上传解决方案事件
            this.$router.push({
                path:'/bkIndex/bkSolveDet', //也可以在路径上拼接
                query:{
                s_k_id:k_id,      //传递的值
                k_status:k_status
                }
            })
        },
        gotoeditknowledge(k_id){//修改问题描述
            this.$router.push({
                path:'/bkIndex/selKLDet', //也可以在路径上拼接
                query:{
                s_k_id:k_id,      
                }
            })
        },
        FnAdd(k_id){//点击新增按钮事件
            this.$router.push('/bkIndex/bkKLDet')
        },
        Fnknowledgelist(){//查询知识库列表
            url.knowledgelist({
                page:this.currentPage,
                per_page:this.PageSize,//每页显示条数
            })
            .then(res=>{
                console.log(res,'--------知识库查询结果')
                if(res.data.code[0]='200'){
                    let xx=res.data.data
                    let newlist=[]
                    xx.forEach(element => {
                        element.fields.k_id=element.pk
                        newlist.push(element.fields)
                    });
                    this.tableData=newlist
                    this.total=res.data.datacount
                }
            })
        },
        Fngitodetails(k_id){//去详情页面
            this.$router.push({
                path:'/bkIndex/details', //也可以在路径上拼接
                query:{
                k_id:k_id,      //传递的值
                k_type:'知识库'
                }
            })
        },
        Fndeleteknowledge(k_id){//删除
            url.deleteknowledge({
                "user_id": this.buserInfo.user_id,
                "k_id": k_id,
            }).then(res=>{
                console.log(res,'--------------删除操作')
                if(res.data.code[0]='200'){
                    this.$message.success(res.data.message[0]);
                    this.Fnknowledgelist()//调用知识库查询结果
                }
            })
        },
        handleCurrentChange(val) {//点击分页功能实现数据切换
            this.currentPage=val
            this.Fnknowledgelist()
        }
    },
    mounted(){
        this.Fnknowledgelist()//调用知识库查询结果
    },
    computed:{
        ...mapState(['buserInfo'])
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.WOBox
    padding 30px
    .operation
        margin 0 0 20px 15px
</style>


